import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { Moment } from 'moment';

export interface CourseItem
{
  CourseID;
  CourseName: string;
  OrderNo: number;
  IsCourseSelected: boolean;
}
export class LotteryRequestForm extends UntypedFormGroup
{
  public readonly ContactsList: UntypedFormArray;
  constructor()
  {
    super(
      {
        DesiredDate: new UntypedFormControl('', [Validators.required]),
        LotteryDetails: new LotteryDetailsFormGroup(),
        ContactsList: new UntypedFormArray([], Validators.required),
      }
    );
    this.ContactsList = this.get('ContactsList') as UntypedFormArray;
  }

  resetContactList(number: number)
  {
    this.ContactsList.clear();
    Array(number).fill(0).map((_) => this.ContactsList.push(new UntypedFormControl()));
  }
}

export class LotteryDetailsFormGroup extends UntypedFormGroup
{
  constructor()
  {
    super(
      {
        DesiredTime: new UntypedFormControl(new Date(), [Validators.required]),
        EarliestTime: new UntypedFormControl(new Date(), [Validators.required]),
        LatestTime: new UntypedFormControl(new Date(), [Validators.required]),
        NumberOfSelectedGroup: new UntypedFormControl(1, [Validators.required]),
        Courses: new CoursesFormArray(),
        PrioritizeBy: new UntypedFormControl(0, [Validators.required]),
        Consecutive: new UntypedFormControl(false),
        LotteryID: new UntypedFormControl(),
        PlayerType: new UntypedFormControl(1)
      }
    );
  }
}

export class Course
{
  CourseID: string;
  CourseName: string;
  OrderNo: number;
  IsCourseSelected: boolean;
  IsFivePlayerTemplate: boolean = false;
}

export class LotteryRequestPlayer
{
  value: number;
  label: string;
}

export class CoursesFormArray extends UntypedFormArray
{
  constructor()
  {
    super([], [Validators.required]);
  }

  public setValue(value: Course[]): void
  {
    value.forEach(v =>
    {
      const course = new CourseFormGroup(v);
      this.push(course);
    });
  }
}

export class CourseFormGroup extends UntypedFormGroup
{
  constructor(course: Course)
  {
    super({
      CourseID: new UntypedFormControl(course.CourseID),
      CourseName: new UntypedFormControl(course.CourseName),
      OrderNo: new UntypedFormControl(course.OrderNo),
      IsCourseSelected: new UntypedFormControl(course.IsCourseSelected),
      IsFivePlayerTemplate: new UntypedFormControl(course.IsFivePlayerTemplate)
    });
  }
}

