import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormArray } from '@angular/forms';
import { MatLegacyButton as MatButton } from '@angular/material/legacy-button';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { CourseItem } from '../../@intermediate/lottery-request';

@Component({
  selector: 'tei-booking-lottery-course-picker-dialog',
  templateUrl: './course-picker-dialog.component.html',
  styleUrls: ['./course-picker-dialog.component.scss']
})
export class CoursePickerDialogComponent implements OnInit
{

  constructor(
    public dialogRef: MatDialogRef<CoursePickerDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { allCourses: CourseItem[], selectedCourses: UntypedFormArray; },
    private textTranslateService: TranslateService,
  ) { }
  numberOfCourses: number;

  ngOnInit(): void
  {
    this.numberOfCourses = this.data.allCourses.length;
  }

  get numberOfSelectedCourses()
  {
    return this.data.allCourses.filter(item => item.OrderNo > 0).length;
  }

  courseChange(courses: CourseItem[])
  {
    courses = this.data.allCourses
      .filter((v, i, a) => a.findIndex(v2 => v2.CourseID === v.CourseID) === i)
      .filter(course => course.OrderNo)
      .sort((a, b) => a.OrderNo - b.OrderNo); // Sort by OrderNo (ascending)

    this.data.selectedCourses.clear();
    this.data.selectedCourses.setValue(courses);
  }

  confirmClick()
  {
    this.dialogRef.close({ courses: this.data });
  }

  disableConfirmButton()
  {
    return this.numberOfSelectedCourses === 0;
  }


  resetSelectedItems()
  {
    this.data.allCourses.map(item => item.OrderNo = 0);
    this.data.selectedCourses.clear();
  }

  getConfirmationText(confirmButton: MatButton)
  {
    const buttonText = confirmButton.disabled ? 'LOTTERY.GENERAL.COURSE_REQUIRED' : 'LOTTERY.GENERAL.CONFIRM';
    return this.textTranslateService.instant(buttonText);
  }

  closeDialog()
  {
    this.dialogRef.close({ courses: this.data });
  }
}
