import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CourseItem } from '../../@intermediate/lottery-request';

@Component({
  selector: 'tei-booking-lottery-course-item',
  templateUrl: './course-item.component.html',
  styleUrls: ['./course-item.component.scss']
})
export class CourseItemComponent implements OnInit {

  constructor() { }
  @Input() course: CourseItem;
  @Input() index: number;
  @Input() view: 'EDIT' | 'DETAIL' | 'SUMMERY' = 'EDIT';
  @Output() courseClick: EventEmitter<CourseItem> = new EventEmitter();
  ngOnInit(): void {
  }

  clickCourse(course: CourseItem)
  {
    if (this.view !== 'EDIT')
    {
      return;
    }
    this.courseClick.emit(course);
  }
}
