import { DatePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { Observable } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { GetLotteryConfirmationDetailsRequest, GetLotteryConfirmationDetailsResponse } from '../../../@intermediate/lottery-request';
import { ILotteryRequestService } from '../../../@intermediate/lottery-request/lottery-request.interface';
@Component({
  selector: 'tei-booking-lottery-lottery-request-confirmation',
  templateUrl: './lottery-request-confirmation.component.html',
  styleUrls: ['./lottery-request-confirmation.component.css']
})
export class LotteryRequestConfirmationComponent implements OnInit
{

  constructor(private datePipe: DatePipe,
    private route: ActivatedRoute,
    private teiTextTranslateService: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    @Inject('ILotteryRequestService') private lotteryRequestService: ILotteryRequestService
  ) { }

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.teiTextTranslateService.instant('LOTTERY.LOTTERY_REQUEST.MAKE_A_BOOKING'),
      relativeUrl: ''
    },
    {
      title: this.teiTextTranslateService.instant('LOTTERY.LOTTERY_REQUEST.TEE_TIME_LOTTERY'),
      relativeUrl: '../../'
    },
    {
      title: this.teiTextTranslateService.instant('LOTTERY.LOTTERY_REQUEST.CONFIRMATION_TITLE'),
      relativeUrl: ''
    },
  ];

  submittedDate = '';
  confirmationDescription: string;
  confirmationDetails$: Observable<GetLotteryConfirmationDetailsResponse>;

  ngOnInit(): void
  {
    this.confirmationDetails$ = this.route.params.pipe(switchMap((res: GetLotteryConfirmationDetailsRequest) => this.lotteryRequestService.GetLotteryConfirmationDetails(res)), tap((res) =>
    {
      this.confirmationDescription = this.teiTextTranslateService.instant('LOTTERY.LOTTERY_REQUEST.CONFIRMATION_DESCRIPTION');
    }));
  }

  navigateTo(path: string)
  {
    this.router.navigate([path], { relativeTo: this.activatedRoute });
  }

  prioritizedByTranslation(prioritizeBy: number)
  {
    return prioritizeBy === 1 ? this.teiTextTranslateService.instant('LOTTERY.GENERAL.TIME') : this.teiTextTranslateService.instant('LOTTERY.LOTTERY_REQUEST.COURSE');
  }

  noneConsecutiveBookings(allowNonConsecutiveBookings: boolean)
  {
    const stringToTranslate = allowNonConsecutiveBookings ? 'LOTTERY.LOTTERY_REQUEST.ALLOWED' : 'LOTTERY.LOTTERY_REQUEST.NOT_ALLOWED';
    return this.teiTextTranslateService.instant(stringToTranslate);
  }
}
