import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CourseItem } from '../../@intermediate/lottery-request';

@Component({
  selector: 'tei-booking-lottery-course-items',
  templateUrl: './course-items.component.html',
  styleUrls: ['./course-items.component.scss']
})
export class CourseItemsComponent implements OnInit
{

  constructor() { }
  get courses(): CourseItem[]
  {
    return this.localCourses;
  }
  @Input() set courses(value: CourseItem[])
  {
    this.localCourses = value;
  }
  @Output() courseChange: EventEmitter<CourseItem[]> = new EventEmitter();
  @Input() view: 'EDIT' | 'DETAIL' | 'SUMMERY' = 'EDIT';

  selectedCourses: CourseItem[] = [];
  localCourses: CourseItem[];

  ngOnInit(): void
  {
  }


  courseClick(course: CourseItem): void
  {
    if (course.OrderNo === 0)
    {
      this.addCourseToSelectedCourses(course);
    } else if (course.OrderNo > 0)
    {
      this.orderCourseItems(course);
    }
    this.courseChange.emit(this.selectedCourses);
  }

  addCourseToSelectedCourses(course: CourseItem): void
  {
    // Get the max courses selected value and sum with one.
    course.OrderNo = Math.max(...this.courses.map((elem) => elem.OrderNo)) + 1;
    this.selectedCourses.push(course);
  }

  orderCourseItems(course: CourseItem)
  {
    this.selectedCourses = this.selectedCourses.filter((item) => item.CourseID !== course.CourseID);
    course.OrderNo = 0;
    const tempSource = [...this.courses].sort((a, b) => a.OrderNo < b.OrderNo ? -1 : 1);
    let select = 0;
    tempSource.map((item) =>
    {
      if (item.OrderNo > 0)
      {
        item.OrderNo = select = select + 1;
      }
      return item;
    });
  }

  sortCoursesByCourseProperties(prop: string)
  {
    if (this.view === 'SUMMERY')
    {
      return [...this.courses].sort((a, b) => a[prop] < b[prop] ? 1 : a[prop] === b[prop] ? 0 : -1);
    } else if (this.view === 'DETAIL')
    {
      return [...this.courses].sort((a, b) => (Number(a[prop] === 0) - Number(b[prop] === 0)) || a[prop] - b[prop]);
    } else if (this.view === 'EDIT')
    {
      return this.courses;
    }
  }
}
