import { Component, EventEmitter, Inject, Injectable, Input, OnInit, Optional, Output } from '@angular/core';
import { ControlContainer, FormGroupDirective, UntypedFormControl } from '@angular/forms';
import { MAT_MOMENT_DATE_ADAPTER_OPTIONS, MomentDateAdapter } from '@angular/material-moment-adapter';
import { DateAdapter, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatDatepicker, MatDatepickerInputEvent } from '@angular/material/datepicker';
import { ResponsiveService } from '@tei/core';

@Injectable()
export class MyDateAdapter extends MomentDateAdapter
{
  constructor(
    @Inject(MAT_DATE_LOCALE) locale: string,
    @Optional() @Inject(MAT_MOMENT_DATE_ADAPTER_OPTIONS) private options?: any,
  )
  {
    super(locale, options);
  }

  format(date: moment.Moment, displayFormat: string): string
  {
    if (this.locale.includes("fr"))
    {
      return date.format('ddd, MMM D, YYYY');
    } else
    {
      return date.format('ddd, MMM D, YYYY');
    }
  }

  setDateFormat(format: string)
  {
    this.setLocale(this.locale);

    this.setDateFormat(format);
  }
}

@Component({
  selector: 'tei-booking-lottery-request-date',
  templateUrl: './lottery-request-date.component.html',
  styleUrls: ["./lottery-request-date.component.scss"],
  providers: [
    {
      provide: DateAdapter,
      useClass: MyDateAdapter,
    },
    {
      provide: MAT_DATE_LOCALE, useValue: JSON.parse(localStorage.getItem('profile-settings')) ?
        JSON.parse(localStorage.getItem('profile-settings'))?.language : 'en'
    }
  ],
  viewProviders: [
    {
      provide: ControlContainer,
      useExisting: FormGroupDirective
    }
  ],
})
export class LotteryRequestDateComponent implements OnInit
{
  @Input() minDate: string;
  @Input() maxDate: string;
  @Input() formControlObject: UntypedFormControl;
  @Output() dateSelected: EventEmitter<Date | undefined> = new EventEmitter();

  onDateChange(type: string, event: MatDatepickerInputEvent<string, string | null>)
  {
    this.dateSelected.emit(this.formControlObject.value);
  }

  openDatePicker(picker: MatDatepicker<any>)
  {
    picker.open();
  }

  ngOnInit(): void {
    const currentValue = this.formControlObject.value;
  
    if (!currentValue) {
      this.formControlObject.setValue(this.stripTimeZone(this.minDate));
    } else {
      this.formControlObject.setValue(this.stripTimeZone(currentValue));
    }
  
    this.dateSelected.emit(this.formControlObject.value);
  }

  private stripTimeZone(value: any): Date | null {
    if (!value) return null;
  
    if (typeof value === 'string') {
      // Extract just the YYYY-MM-DD part before the "T"
      const dateOnly = value.split('T')[0];
      const [year, month, day] = dateOnly.split('-').map(Number);
      return new Date(year, month - 1, day);
    }
  
    if (value instanceof Date) {
      return new Date(value.getFullYear(), value.getMonth(), value.getDate());
    }
  
    return null;
  }

}
