import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./pages/lottery-request/lottery-request.module').then(m => m.LotteryRequestModule),
    data: { RouteData: new RouteData({ Title: 'Lottery Request' }) }
  },
  {
    path: ':dd/:mm/:yy/:isedit',
    loadChildren: () => import('./pages/lottery-request/lottery-request.module').then(m => m.LotteryRequestModule),
    data: { RouteData: new RouteData({ Title: 'Lottery Request' }) }
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LotteryRoutingModule
{
}
