import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { PostContactSelectionWrapperRequest, WrapperSubmitCallbackArgs } from '@tei/common-contact-lookup-shared';
import { BreadCrumb } from '@tei/material';
import moment from 'moment';
import { Observable, Subscription } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { Course, GetLotteryRequestDesireDateRangeRequest, GetLotteryRequestDesireDateRangeResponse, GetLotteryRequestStepDataByDesireDateResponse, LotteryRequestForm, LotteryRequestGroups, LotteryRequestPlayer, PlayerType } from '../../@intermediate/lottery-request';
import { ILotteryRequestService } from '../../@intermediate/lottery-request/lottery-request.interface';
@Component({
  selector: 'tei-booking-lottery-request',
  templateUrl: './lottery-request.component.html',
  styleUrls: ['./lottery-request.component.scss']
})
export class LotteryRequestComponent implements OnInit
{
  startTime: Date = new Date(1995, 11, 17, 0, 0, 0);
  endTime: Date = new Date(1995, 11, 17, 23, 45, 0);
  form: LotteryRequestForm = new LotteryRequestForm();
  contactLookupState = false;
  submitLotteryRequestLoading = false;
  contactLookUpConfig: any;
  detailsFormSubscription: Subscription;
  day: number;
  month: number;
  year: number;
  playerType: string;

  constructor(
    @Inject('ILotteryRequestService') private lotteryRequestService: ILotteryRequestService,
    private router: Router,
    private activeRoute: ActivatedRoute,
    private translate: TranslateService
  )
  {
    //Array(this.getDetails.get('Players').value).fill(0).map((_) => (this.form.get('ContactsList') as FormArray).push(new FormControl()));

    this.activeRoute.params.subscribe(params =>
    {
      this.day = params.dd;
      this.month = params.mm;
      this.year = params.yy;
    });

  }

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('LOTTERY.LOTTERY_REQUEST.MAKE_A_BOOKING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('LOTTERY.LOTTERY_REQUEST.TEE_TIME_LOTTERY'),
      relativeUrl: ''
    },
  ];

  lotteryDetail: Observable<GetLotteryRequestStepDataByDesireDateResponse>;
  daysOutFromPresentDay$: Observable<GetLotteryRequestDesireDateRangeResponse>;
  desiredTimeErrorMessage = '';
  LatestTimeErrorMessage = '';
  ngOnInit(): void
  {
    this.daysOutFromPresentDay$ = this.lotteryRequestService.GetLotteryRequestDesireDateRange(new GetLotteryRequestDesireDateRangeRequest());
  }

  getDetailsChanged(desireDateRange)
  {
    if (!desireDateRange.EnableEarliestLatest)
    {
      return;
    }
    this.detailsFormSubscription = this.getDetails.valueChanges.subscribe(data =>
    {
      if (data.DesiredTime < data.EarliestTime || data.DesiredTime > data.LatestTime)
      {
        this.desiredTimeErrorMessage = this.translate.instant('LOTTERY.LOTTERY_REQUEST.DESIRED_TIME_ERROR_MESSAGE');

        this.getDetailsFormControlByName('DesiredTime').setErrors({ 'notInRange': true }, { emitEvent: false });
        this.getDetailsFormControlByName('DesiredTime').markAllAsTouched();
      } else
      {
        this.desiredTimeErrorMessage = this.prepareFieldError('DesiredTime', 'notInRange', 'PRISTINE');
      }

      if (data.EarliestTime > data.LatestTime)
      {
        this.LatestTimeErrorMessage = this.prepareFieldError('LatestTime', 'earliestTimeIsGreaterThanLatestTime', 'TOUCHED', 'LOTTERY.LOTTERY_REQUEST.LATEST_TIME_ERROR_MESSAGE');
      } else
      {
        this.LatestTimeErrorMessage = this.prepareFieldError('LatestTime', 'earliestTimeIsGreaterThanLatestTime', 'PRISTINE');
      }
    });
  }

  prepareFieldError(fieldName: string, errorName: string, markAs: 'PRISTINE' | 'TOUCHED', message?: string): string
  {
    let error = {};
    error[errorName] = markAs === 'PRISTINE' ? null : true;
    this.getDetailsFormControlByName(fieldName).setErrors(error, { emitEvent: false });
    if (markAs === 'PRISTINE')
    {
      this.getDetailsFormControlByName(fieldName).updateValueAndValidity({ onlySelf: false, emitEvent: false });
      this.getDetailsFormControlByName(fieldName).markAsPristine();
    } else if (markAs === 'TOUCHED')
    {
      this.getDetailsFormControlByName(fieldName).markAllAsTouched();
    }
    if (message)
    {
      return this.translate.instant(message);
    }
    return message;
  }

  get getDate()
  {

    if (this?.day != null && this?.month != null && this?.year != null)
    {

      const date = new Date(this.year, this.month - 1, this.day);
      this.day = null;
      this.month = null;
      this.year = null;
      this.form.controls["DesiredDate"].setValue(date);
    }

    return this.form.get('DesiredDate') as UntypedFormControl;
  }

  get getDetails()
  {
    return this.form.get('LotteryDetails') as UntypedFormGroup;
  }

  getDetailsFormControlByName(name: string): UntypedFormControl
  {
    return this.getDetails.get(name) as UntypedFormControl;
  }

  get courses()
  {
    return this.getDetails.get('Courses') as UntypedFormArray;
  }

  get contactsList()
  {
    return this.form.get('ContactsList') as UntypedFormArray;
  }

  get LotteryID()
  {
    var lotteryID = (this.getDetails.get('LotteryID') as UntypedFormControl).value;

    if (!lotteryID || lotteryID == '00000000-0000-0000-0000-000000000000')
    {
      return null;
    }
    else
    {
      return lotteryID;
    }
  }

  get getForm()
  {
    return this.form.controls;
  }

  dateSelected(date: Date)
  {
    if (!date)
    {
      return;
    }

    if (this.detailsFormSubscription)
    {
      this.detailsFormSubscription.unsubscribe();
    }
    this.lotteryDetail = this.lotteryRequestService.GetLotteryRequestStepDataByDesireDate(moment(date).format("YYYY-MM-DD").toString()).pipe(
      map(data =>
      {
        const courses: Course[] = data.Courses.map(c => ({ CourseID: c.CourseID, CourseName: c.CourseName, OrderNo: c.OrderNo ? c.OrderNo : 0, IsCourseSelected: c.IsCourseSelected, IsFivePlayerTemplate: c.IsFivePlayerTemplate }));
        const players: LotteryRequestPlayer[] = data.Players.map(p => ({ value: p.value, label: p.label }));
        const playersPerGroup: LotteryRequestPlayer[] = data.PlayersPerGroup.map(p => ({ value: p.value, label: p.label }));

        this.playerType = this.getPlayerType(courses);

        const obj: GetLotteryRequestStepDataByDesireDateResponse = {
          LotteryID: data.LotteryID,
          Courses: courses,
          AllowNonConsecutiveBooking: data.AllowNonConsecutiveBooking,
          EnableEarliestLatest: data.EnableEarliestLatest,
          MaxGroups: data.MaxGroups,
          MinPlayersPerGroup: data.MinPlayersPerGroup,
          PickerEndTime: new Date(data.PickerEndTime),
          PickerIntervals: data.PickerIntervals,
          PickerStartTime: this.setStartTime(data.PickerStartTime, date, data.PickerIntervals),
          Players: players,
          EarliestTime: data.EarliestTime,
          LatestTime: data.LatestTime,
          DesiredTime: data.DesiredTime,
          PrioritizeBy: data.PrioritizeBy,
          NumberOfSelectedGroup: data.NumberOfSelectedGroup,
          PlayersPerGroup: playersPerGroup,
          Consecutive: data.Consecutive
        };

        return obj;
      }),
      tap(data =>
      {
        this.getDetailsChanged(data);
        this.getDetailsFormControlByName('DesiredTime').patchValue(data.DesiredTime?new Date(data.DesiredTime):new Date(data.PickerStartTime));
        this.getDetailsFormControlByName('EarliestTime').patchValue(data.EarliestTime?new Date(data.EarliestTime):new Date(data.PickerStartTime));
        this.getDetailsFormControlByName('LatestTime').patchValue(data.LatestTime?new Date(data.LatestTime):new Date(data.PickerStartTime));
        this.getDetailsFormControlByName('PrioritizeBy').patchValue(data.PrioritizeBy?.toString());
        this.getDetailsFormControlByName('Consecutive').patchValue(data.Consecutive);
        this.getDetails.patchValue({ NumberOfSelectedGroup: data.NumberOfSelectedGroup, LotteryID: data.LotteryID });
        this.courses.clear();
        this.courses.setValue(data.Courses.filter((course, index) => course.OrderNo > 0));
      })
    );
  }


  getPlayerType(courses: Course[]): string
  {
    const hasTrue = courses.some(course => course.IsFivePlayerTemplate === true);
    const hasFalse = courses.some(course => course.IsFivePlayerTemplate === false);

    if (hasTrue && hasFalse)
    {
      return PlayerType.FiveAndFourPlayers;
    }
    else if (hasTrue)
    {
      return PlayerType.FivePlayers;
    }
    else if (hasFalse)
    {
      return PlayerType.FourPlayers;
    }
    else
    {
      return PlayerType.FourPlayers;
    }
  }




  setStartTime(startTime: Date, payload: Date, interval: number): Date
  {
    if (this.isToday(new Date(payload)))
    {
      const today = new Date();
      return new Date(1995, 11, 17, today.getHours(), this.setMinute(today.getMinutes(), interval), 0);
    }
    return new Date(startTime);
  }

  setMinute(minute: number, interval: number): number
  {
    const minutesOutOfNow = minute % interval;
    if (!minutesOutOfNow)
    {
      return minute - minutesOutOfNow;
    }
    return minute - minutesOutOfNow + interval;
  }

  isToday(someDate: Date): boolean
  {
    const today = new Date();
    return someDate.getDate() == today.getDate() &&
      someDate.getMonth() == today.getMonth() &&
      someDate.getFullYear() == today.getFullYear();
  }

  getContactLookupData(groups: PostContactSelectionWrapperRequest): LotteryRequestGroups[]
  {
    let result: LotteryRequestGroups[] = [];
    groups.ContactSelections.forEach((group, index) =>
    {
      const contacts = [];
      group.ContactSelectedResults.forEach((item, index) =>
      {
        contacts.push({
          UserName: item.UserName,
          ContactID: item.ContactID,
          FirstName: item.FirstName,
          LastName: item.LastName,
          Email: item.Email,
          PositionNo: item.PositionNumber,
          IsGuest: item.SelectionType == 2,
          IsHoldTBD: item.SelectionType == 6 || item.ContactID == '88888888-8888-8888-8888-888888888888',
          SendEmailNotification: item.SendNotification
        });
      });

      result.push({
        GroupNo: index + 1,
        Contacts: contacts
      });
    });
    return result;
  }

  submitLotteryRequest(event: WrapperSubmitCallbackArgs, lotteryDetail: GetLotteryRequestStepDataByDesireDateResponse)
  {
    this.submitLotteryRequestLoading = true;
    if (!event)
    {
      return;
    }
    const postLotteryRequest =
      {
        DesiredDate: moment(this.getDate.value).format("YYYY-MM-DD").toString(),
        DesiredTime: moment(this.getDetails.value.DesiredTime).format('HH:mm:ss'),
        EarliestTime: moment(this.getDetails.value.EarliestTime).format('HH:mm:ss'),
        LatestTime: moment(this.getDetails.value.LatestTime).format('HH:mm:ss'),
        Groups: this.getContactLookupData(event.Request),
        Courses: this.courses.value,
        PrioritizeBy: this.getDetails.value.PrioritizeBy,
        AllowNonConsecutiveBookings: this.getDetails.value.Consecutive,
        ContactLookupSelectionGroupUID: event.Response.SelectionGroupUID
      } as any;

    if (this.LotteryID)
    {
      postLotteryRequest.LotteryID = this.LotteryID;
    }

    if (!lotteryDetail.EnableEarliestLatest)
    {
      delete postLotteryRequest.EarliestTime;
      delete postLotteryRequest.LatestTime;
    }
    this.lotteryRequestService.PostLotteryRequestCreate(postLotteryRequest).subscribe(res =>
    {
      this.submitLotteryRequestLoading = false;
      this.router.navigate(['confirmation', res.ConfirmationNumber], { relativeTo: this.activeRoute });
    }, error =>
    {
      this.submitLotteryRequestLoading = false;
    });

  }

  detailsSubmit(event: boolean)
  {
    if (!event)
    {
      return;
    }

    const courses = this.getDetails.value.Courses as Course[];
    const isFivePlayerTemplate = courses.some(f => f.IsFivePlayerTemplate === true);


    var req =
      {
        NumberOfGroups: this.getDetails.value.NumberOfSelectedGroup,
        CourseID: this.getDetails.value.Courses[0].CourseID,
        DesireDate: moment(this.getDate.value).format("YYYY-MM-DD").toString(),
        NumberOfPlayers: isFivePlayerTemplate ? 5 : 4
      } as any;

    if (this.LotteryID)
    {
      req.LotteryID = this.LotteryID;
    }

    this.contactLookupState = false;
    this.lotteryRequestService.GetContactLookupTemplate(req).subscribe(data =>
    {
      this.contactLookUpConfig = data;
      this.contactLookupState = true;
    });
  }

}
