import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { ControlContainer, FormGroupDirective } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ContactSelectionWrapperComponent, WrapperSubmitCallbackArgs } from '@tei/common-contact-lookup-shared';
import { Course, GetLotteryRequestStepDataByDesireDateResponse, LotteryRequestForm } from '../../@intermediate/lottery-request';


@Component({
  selector: 'tei-booking-lottery-request-players',
  templateUrl: './lottery-request-players.component.html',
  viewProviders: [
    {
      provide: ControlContainer,
      useExisting: FormGroupDirective
    }
  ],
  styleUrls: ['./lottery-request-players.component.scss']
})
export class LotteryRequestPlayersComponent
{

  constructor(private translate: TranslateService) { }

  @ViewChild('contactsList') contactsList: ContactSelectionWrapperComponent;
  @Input() form: LotteryRequestForm;
  @Input() config;
  @Input() loading: boolean;
  @Input() apiLotteryDetail: GetLotteryRequestStepDataByDesireDateResponse;
  @Output() onSubmit: EventEmitter<WrapperSubmitCallbackArgs | boolean> = new EventEmitter(false);
  lastSearchRequestTime = new Date();

  get lotteryDetails()
  {
    return this.form.get('LotteryDetails');
  }

  preventMultipleRequestInPeriodOfTime()
  {
    const currentTime = new Date();
    let diff = currentTime.getTime() - this.lastSearchRequestTime.getTime();

    if (diff < 3500)
    {
      return true;
    } else
    {
      this.lastSearchRequestTime = new Date();

      return false;
    }
  }

  get courses(): Course[]
  {
    return this.lotteryDetails.value['Courses'];
  }

  get date()
  {
    return this.form.get('DesiredDate');
  }

  get contactsListFormArray()
  {
    return this.form.get('ContactsList');
  }

  isDisabled()
  {
    return this.contactsList?.form?.invalid || this.loading;
  }

  submit()
  {
    if (this.preventMultipleRequestInPeriodOfTime())
    {
      return;
    }
    this.contactsList.submit();
  }

  contactLookupCallback(event: WrapperSubmitCallbackArgs)
  {
    this.onSubmit.emit(event);
  }

  getPrioritizedBy(value: string)
  {
    return value !== "2" ? this.translate.instant('LOTTERY.GENERAL.TIME') : this.translate.instant('LOTTERY.LOTTERY_REQUEST.COURSE');
  }
}
