import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable, of } from 'rxjs';
import { GetContactLookupTemplateRequest, GetContactLookupTemplateResponse, GetLotteryConfirmationDetailsRequest, GetLotteryConfirmationDetailsResponse, GetLotteryRequestDesireDateRangeRequest, GetLotteryRequestDesireDateRangeResponse, GetLotteryRequestStepDataByDesireDateResponse, PostLotteryRequestRequest, PostLotteryRequestResponse, PrioritizeByEnum } from '../@intermediate/lottery-request';
import { ILotteryRequestService } from '../@intermediate/lottery-request/lottery-request.interface';

@Injectable({
  providedIn: 'root'
})

export class LotteryRequestService implements ILotteryRequestService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl("Booking/Lottery") + "/LotteryRequest";
  }

  GetLotteryRequestDesireDateRange(request: GetLotteryRequestDesireDateRangeRequest): Observable<GetLotteryRequestDesireDateRangeResponse>
  {
    const apiUrl = this.serviceUrl + '/GetLotteryRequestDesireDateRange';
    return this.http.get<GetLotteryRequestDesireDateRangeResponse>(apiUrl, { params: request as any });
  }

  GetLotteryRequestStepDataByDesireDate(request: string): Observable<GetLotteryRequestStepDataByDesireDateResponse>
  {
    const apiUrl = this.serviceUrl + '/GetLotteryRequestStepDataByDesireDate';
    let params = new HttpParams();
    params = params.append('DesireDate', request);
    return this.http.get<GetLotteryRequestStepDataByDesireDateResponse>(apiUrl, { params });
  }

  GetLotteryConfirmationDetails(request: GetLotteryConfirmationDetailsRequest): Observable<GetLotteryConfirmationDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetLotteryConfirmationDetails';
    return this.http.get<GetLotteryConfirmationDetailsResponse>(apiUrl, { params: request as any });
  }

  GetContactLookupTemplate(request: GetContactLookupTemplateRequest): Observable<GetContactLookupTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '/GetContactLookupTemplate';
    return this.http.get<any>(apiUrl, { params: request as any });
  }

  PostLotteryRequestCreate(request: PostLotteryRequestRequest): Observable<PostLotteryRequestResponse>
  {
    const apiUrl = this.serviceUrl + '/PostLotteryRequestCreate';
    return this.http.post<PostLotteryRequestResponse>(apiUrl, request);
  }
}
