import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormArray, UntypedFormArray, UntypedFormControl } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacySelectChange as MatSelectChange } from '@angular/material/legacy-select';
import { Course, GetLotteryRequestStepDataByDesireDateResponse, LotteryRequestForm, PlayerType } from '../../@intermediate/lottery-request';
import { AllowNoneConsecutiveBookingsDialogComponent } from '../allow-none-consecutive-bookings-dialog/allow-none-consecutive-bookings-dialog.component';
import { CoursePickerDialogComponent } from '../course-picker-dialog/course-picker-dialog.component';
import { PrioritizeByTimeOrCourseDialogComponent } from '../prioritize-by-time-or-course-dialog/prioritize-by-time-or-course-dialog.component';

@Component({
  selector: 'tei-booking-lottery-details',
  templateUrl: './lottery-details.component.html',
  styleUrls: ['./lottery-details.component.scss'],
})
export class LotteryDetailsComponent implements OnInit
{
  courses: Course[];
  selectedCourses: Course[];
  lastRemovedCourseControl: any[] = [];
  constructor(public dialog: MatDialog) { }

  ngOnInit(): void 
  {
    this.checkNumberOfCourses();
  }

  @Input() lotteryDetail: GetLotteryRequestStepDataByDesireDateResponse;
  @Input() form: LotteryRequestForm;
  @Input() desiredTimeErrorMessage = '';
  @Input() LatestTimeErrorMessage = '';
  @Input() playerType: string;
  @Output() detailsSubmit: EventEmitter<boolean> = new EventEmitter<boolean>(false);

  getFormControlByName(name: string): UntypedFormControl
  {
    return this.detailsForm.get(name) as UntypedFormControl;
  }

  get requestDetails()
  {
    return this.form.get('DesiredDate').value;
  }

  get detailsForm()
  {
    return this.form.get('LotteryDetails');
  }

  openCoursePickerDialog()
  {
    const coursePickerDialogRef = this.dialog.open(CoursePickerDialogComponent, {
      data: {
        allCourses: this.courses,
        selectedCourses: this.detailsForm.get('Courses') as UntypedFormArray
      },
      panelClass: ['o-dialog-sm-fullscreen'],
      maxWidth: '95vw',
      width: '500px'
    });
  }

  openPrioritizeByTimeOrCourseDialog()
  {
    this.dialog.open(PrioritizeByTimeOrCourseDialogComponent, {
      data: this.lotteryDetail.Courses,
      maxWidth: '95vw',
      width: '380px'
    });
  }

  openAllowNoneConsecutiveBookingsDialog()
  {
    this.dialog.open(AllowNoneConsecutiveBookingsDialogComponent, {
      data: this.lotteryDetail.Courses,
      maxWidth: '95vw',
      width: '380px'
    });
  }

  clickAutoPick()
  {

  }

  prioritizeByTimeOrCourseWhatIsThisClick(event: MouseEvent)
  {
    event.preventDefault();
    this.openPrioritizeByTimeOrCourseDialog();
  }

  allowNoneConsecutiveWhatIsThisClick(event: MouseEvent)
  {
    event.preventDefault();
    this.openAllowNoneConsecutiveBookingsDialog();
  }

  onPlayersNumberChange(event: MatSelectChange)
  {
    this.form.resetContactList(event.value);
  }

  onPlayerTypeChange(event: MatSelectChange)
  {
    const isFivePlayerTemplate = this.lotteryDetail.Courses.some(s => s.IsFivePlayerTemplate === true);
    let coursesArray = this.form.get('LotteryDetails.Courses') as FormArray;

    if (!this.isEdit())
    {
      this.clearSelectedCourse();
    }
    else
    {
      const value = this.selectedCourses.some(a => a.IsFivePlayerTemplate) ? 2 : 1;
      if (value != event.value)
      {
        this.updateFormArrayWhenEditPlayers(coursesArray);
      }
      else
      {
        if (this.lastRemovedCourseControl.length > 0)
        {
          this.lastRemovedCourseControl.forEach(control =>
          {
            coursesArray.push(control);
          });
        }

        this.lastRemovedCourseControl = [];
      }
    }
    this.updateCourseListBasedOnPlayerSelection(event);
  }


  private updateCourseListBasedOnPlayerSelection(event: MatSelectChange)
  {
    if (event.value === 1)
    {
      // four players
      this.courses = this.lotteryDetail.Courses.filter(x => x.IsFivePlayerTemplate === false);
    }
    else
    {
      // five players
      this.courses = this.lotteryDetail.Courses.filter(x => x.IsFivePlayerTemplate === true);
    }
  }

  private updateFormArrayWhenEditPlayers(coursesArray: FormArray<any>)
  {
    for (let i = coursesArray.length - 1; i >= 0; i--)
    {
      const control = coursesArray.at(i);
      const formCourseID = control.get('CourseID')?.value;
      const matchingCourse = this.selectedCourses.find(c => c.CourseID === formCourseID);

      if (matchingCourse)
      {
        const removedControl = coursesArray.at(i);
        coursesArray.removeAt(i);
        this.lastRemovedCourseControl.push(removedControl);
      }
    }
  }

  clearSelectedCourse()
  {
    const coursesArray = this.form.get('LotteryDetails.Courses') as FormArray;

    if (coursesArray?.value?.length > 0)
    {
      coursesArray?.controls?.forEach(control =>
      {
        const formCourseID = control.get('CourseID')?.value;

        const matchingCourse = this.lotteryDetail.Courses.find(c => c.CourseID === formCourseID);

        if (matchingCourse)
        {
          matchingCourse.OrderNo = 0;
        }
      });
    }
  }

  onDetailsSubmit()
  {
    this.detailsSubmit.emit(true);
  }

  checkNumberOfCourses()
  {
    this.courses = this.lotteryDetail.Courses;
    if (this.lotteryDetail?.Courses?.length === 1)
    {
      this.lotteryDetail.Courses[0].OrderNo = 1;
      (this.detailsForm.get('Courses') as UntypedFormArray).setValue(this.courses);
    }

    this.updatePlayersPerGroupValue();
  }

  private updatePlayersPerGroupValue()
  {
    const playerTypeControl = this.form.get('LotteryDetails.PlayerType');
    if (this.isEdit())
    {
      const selectedCourse = this.lotteryDetail.Courses.find(f => f.OrderNo > 0);
      const isFivePlayerTemplate = selectedCourse?.IsFivePlayerTemplate ?? false;
      const patchedValue = isFivePlayerTemplate ? 2 : 1;
      playerTypeControl?.patchValue(patchedValue);

      this.selectedCourses = this.lotteryDetail.Courses.filter(f => f.IsCourseSelected);

      this.onPlayerTypeChange({ value: patchedValue } as MatSelectChange);
    }
    else if (this.playerType === PlayerType.FiveAndFourPlayers)
    {
      playerTypeControl?.patchValue(1);
      this.onPlayerTypeChange({ value: 1 } as MatSelectChange);
    }
  }

  isEdit(): boolean
  {
    return this.lotteryDetail.LotteryID ? true : false;
  }

}
